/* src/Weather.css */
.weather-app {
  text-align: center;
  margin-top: 40px;
}

.weather-info {
  margin-top: 40px;
  padding: 20px;
  background: linear-gradient(to right, #fbc2eb, #a6c1ee);
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: #333; /* Text color for the weather info */
}

.weather-info h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.weather-details {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  font-size: 18px;
  margin-top: 20px;
}

.weather-details p {
  margin: 5px;
}

input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  font-size: 16px;
  width: 100%;
  max-width: 300px;
}

button {
  padding: 10px 20px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  background-image: linear-gradient(to right, #007bff, #00c6ff);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  outline: none;
  margin-top: 10px; /* Add margin to create space between input and button */
}

button:hover {
  background-color: #0056b3;
}

.weather-icon {
  font-size: 48px;
  margin-right: 10px;
}

/* Center content vertically and horizontally */
html,
body {
  height: 100%;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, #f9f9f9, #ececec);
  margin: 0;
  font-family: Arial, sans-serif;
  color: #333; /* Default text color */
}

.weather-app {
  max-width: 400px;
  padding: 20px;
}

/* Responsive Styles */
@media (max-width: 600px) {
  .weather-details {
    flex-direction: column;
    align-items: center;
  }

  .weather-details p {
    margin: 10px 0;
  }
}
